import { useEffect, useState } from "react";
import { teams } from "../data/teams";
import { FaHeart, FaRegHeart } from "react-icons/fa";

export default function TeamsPage() {
  const [favorites, setFavorites] = useState([]);

  // Load favorites from localStorage
  useEffect(() => {
    const saved = JSON.parse(localStorage.getItem("favorites")) || [];
    setFavorites(saved);
  }, []);

  // Add / Remove from favorites
  const toggleFavorite = (teamId) => {
    let updatedFavorites;

    if (favorites.includes(teamId)) {
      updatedFavorites = favorites.filter((id) => id !== teamId);
    } else {
      updatedFavorites = [...favorites, teamId];
    }

    setFavorites(updatedFavorites);
    localStorage.setItem("favorites", JSON.stringify(updatedFavorites));
  };

  return (
    <div className="max-w-7xl mx-auto px-6 pt-10">
      <h1 className="text-4xl font-bold mb-6 text-white">Teams</h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-8">
        {teams.map((team) => {
          const isFav = favorites.includes(team.id);

          return (
            <div
              key={team.id}
              className="bg-gray-800 p-6 rounded-xl shadow-lg hover:shadow-blue-500/30 transition transform hover:-translate-y-1"
            >
              <img
                src={team.logo}
                alt={team.name}
                className="w-24 h-24 object-contain mx-auto"
              />

              <h2 className="text-xl text-center font-semibold text-white mt-4">
                {team.name}
              </h2>

              <p className="text-center text-gray-400">{team.sport}</p>

              {/* Favorite Button */}
              <button
                onClick={() => toggleFavorite(team.id)}
                className="flex justify-center mt-4 text-2xl"
              >
                {isFav ? (
                  <FaHeart className="text-red-500 scale-110 transition" />
                ) : (
                  <FaRegHeart className="text-gray-400 hover:text-red-400 transition" />
                )}
              </button>
            </div>
          );
        })}
      </div>
    </div>
  );
}
